<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p>
                <span class="tip">Proxy</span> 
            </p>
            <pre class="code"><span class="com">/* targethandler */</span><br><span class="key">var </span><span class="param">proxy</span><span class="key"> = new </span><span class="method">Proxy</span><span class="word">(target,handler);</span></pre>
            <p>
                 <span class="tip">Proxy</span>  <span class="tip">Object</span> 
            </p>
            <el-table :data="table_104">
                <el-table-column prop="name" label="">
                    <template scope="scope">
                        <span v-html="scope.row.name">{{scope.row.name}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="intro" label="">
                    <template scope="scope">
                        <span v-html="scope.row.intro">{{scope.row.intro}}</span>
                    </template>
                </el-table-column>
            </el-table>
            <p>
                
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


